<template>
    <div>
        <van-nav-bar class="hader" title="登录" left-text="返回" left-arrow @click-left="back()">
            <a href="http://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/guanfan_v1.0.6.apk" slot="right"
               v-if="isiOS != true && isWeixin != true">
                <van-button type="primary" size="small">安装APP</van-button>
            </a>
            <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
                <van-button type="primary" size="small">安装APP</van-button>
            </a>
            <a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect"
               slot="right" v-if="isWeixin == true">
                <van-button type="primary" size="small">关注</van-button>
            </a>
        </van-nav-bar>
        <div class="text-center title">
            <div class="text-black text-bold text-25">欢迎饭友回家</div>
            <div class="grayColor text-16 mt25">闲来无事，就想做菜给你吃</div>
        </div>
        <van-cell-group class="padding-lr">
            <van-field
                    v-model="ruleForm.phone"
                    clearable
                    left-icon="phone-o"
                    placeholder="请输入手机号"
            />

            <van-field
                    v-model="ruleForm.password"
                    center
                    clearable
					left-icon="closed-eye"
                    placeholder="请输入密码"
            >
            </van-field>
        </van-cell-group>

        <div class="padding-lr">
            <div class="clearfix tipText text-14 mt15">
                    <div class="left" @click="tocodel">验证码登录</div>
            </div>
            <van-button type="info" size="large" @click="bindLogin" :disabled="!isSubmit" class="mt15 bg-yellow">登录</van-button>
            <van-button type="info" size="large" plain @click="toreg"  class="mt15 bg-yellow">注册</van-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'vant';
    export default {
        name: "pswLogin",
        data() {
            return {
                ruleForm: {
                    phone: '',
                    password: '',
                    type: 1
                },
                isWeixin: '',
                isiOS: '',
            }
        },
       computed: {
           isSubmit() {
               return !!this.ruleForm.phone && !!this.ruleForm.password
           }
       },
        created() {
            this.getwx();
            this.appSource();
        },
        methods: {
            getwx() {
                var ua = window.navigator.userAgent.toLowerCase();
                if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                    this.isWeixin = true;
                } else {
                    this.isWeixin = false;
                }
            },
            appSource() {
                const u = navigator.userAgent,
                    app = navigator.appVersion;
                if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
                    this.isiOS = true;
                } else {
                    this.isiOS = false;
                }
            },
            bindLogin() {
                if (this.ruleForm.phone.length < 11) {
                    Toast('手机号不能超过11位');
                    return;
                } else if (this.ruleForm.password.length < 6) {
                    Toast('密码不能少于6位');
                    return;
                }
                this.$store.dispatch('LoginByPsw', this.ruleForm).then(() => {
                    this.getUser()
                }).catch(() => {
                })
            },
            getUser() {
                this.$store.dispatch('GetUserInfo').then(response => {
                    this.$router.go(-1);//返回上一层
                }).catch(error => {
                })
            },
			toreg(){
				this.$router.replace({path: this.redirect || '/reg'});
			},
			tocodel(){
				this.$router.replace({path: this.redirect || '/codeLogin'});
			},
			back(){
			    this.$router.go(-1);//返回上一层
			},
        }
    }
</script>

<style scoped>
    .title {
        padding: 10rem 0 5rem;
    }

    .van-button--small {
        height: 28px;
    }

    .van-button--primary.verBtn {
        color: black;
        border-radius: 0.5rem;
        background-color: #fff;
        border: 1px solid black;
    }

    .van-button--info {
        border: none;
    }

    .van-button--plain.bg-yellow {
        background-color: white;
        border: 1px solid #FFC700;
        border-radius: 0.5rem;
        color: #FFC700
    }

    .tipText {
        height: 2rem;
        line-height: 2rem;
        color: black;
    }
</style>

